*{
    box-sizing:border-box;margin:0;padding:2px
}

.prime1,body{
    background-image: url('hero-bg.jpg');
}

.prime2{
    background-color:#50fff9
}
.allfont
{font-family:Segoe UI,Tahoma,Geneva,Verdana,sans-serif;font-style:oblique;font-weight:bolder}
.data .first .item
{background:linear-gradient(10deg,#ea5353 10%,#6d3c3c 81%);color:#fff}
.main
{border:1px solid #9b1212;color:#fff;text-align:center}
::-webkit-scrollbar
{width:12px}
::-webkit-scrollbar-thumb
{background:linear-gradient(10deg,#b149c9 10%,#8d076e 81%);border-radius:5px}
::-webkit-scrollbar-thumb:hover
{background:linear-gradient(transparent,#2a9512)}